.page {
  background: $branding-color-gray;
  font-family: 'Poppins', sans-serif;
  color: #fff;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
  }

  &__header {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 100px;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: $transition;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 90px;

    @include onTablet {
      padding-top: 20px;
      margin-bottom: 100px;
    }

    @include onDesktop  {
      margin-bottom: 140px;
    }

    &-title {
      @extend %section-title;

      text-align: center;
      margin: 0 0 48px;

      @include onTablet {
       margin-bottom: 56px;
      }
    }
  }

}
